<script>
    import { reactive, onMounted, toRefs, nextTick,getCurrentInstance } from 'vue'
    import { useRouter } from 'vue-router'
    import { Toast } from 'vant'

    import sHeader from '@/components/SimpleHeader'
    export default {
        setup() {
            const router = useRouter()
            const {appContext}=getCurrentInstance();
            const state = reactive({
                powerValue:450,
                rechargeAmount:'',
                rechargeAmountStatus:false,
                payResult:false,
                show:false,
                modal_show_type:'',
                showOverlay:false,
                exchangeCode:'',
                remindTime:0,
                exchangeStatus:'0',
                handletype:'',
                onsalecheck:false,
                captionList:[
                    {question:'什么是能量值？',answer:'能量值反映了您在燃象平台中的生命力指数，能量值越高，越活跃。'},
                    {question:'能量值有什么用？',answer:'您可以通过能量值参与特别的线下活动或是兑换实物奖励。'},
                    {question:'能量值有有效期吗？',answer:'能量值为终身有效，最终解释权归燃象所有。'}
                ],
                powerList:[
                    {info:'充值（支付宝）',value:'+200',time:'2022-09-08 11:01'},
                    {info:'获得第一个燃身份',value:'+150',time:'2022-09-06 15:31'},
                    {info:'消费兑换',value:'-200',time:'2022-09-03 4:21'},
                    {info:'新用户奖励',value:'+300',time:'2022-09-01 00:01'}
                ],


                product: {
                },
                modal_show:false,
                active:0,
                payWayTabs:[
                    {
                        title:'银行卡',
                        img: require("../../assets/image/btn_cardPay.png"),
                    },
                    {
                        title:'微信支付',
                        img: require("../../assets/image/btn_weixinPay.png"),
                    },
                    {
                        title:'支付宝',
                        img: require("../../assets/image/btn_zfbPay.png"),
                    }]
            })

            onMounted(async () => {

                let dataInfo={

                    author:'Mee Cats',
                    company:'燃象',
                    id: 5,
                    title:'Astronaut',
                    pic: require("../../assets/image/pic_4.jpg"),
                    price:'69.9'

                }
                const {data} = {
                    "resultCode": 200,
                    "message": "SUCCESS",
                    "data": dataInfo
                };

                state.product = data
                // Toast.clear()
            })

            nextTick(() => {
                const content = document.querySelector('.detail-content')
                content.scrollTop = 0
            })

            const goBack = () => {
                router.go(-1)
            }
            const onInput = () => {
                if(state.exchangeCode=='') state.exchangeStatus='0';
                else state.exchangeStatus='1';
            }
            const exchangeCodeHandle=()=>{
                if(state.exchangeCode=='111'){
                    state.exchangeStatus='2';
                    state.powerValue=parseInt(state.powerValue)+10;
                    state.powerList.push({info:`消费兑换`,value:'+10',time:appContext.config.globalProperties.$moment(new Date()).format('YYYY.MM.DD hh:mm')})

                }
                else state.exchangeStatus='3';
            }
            const goToArtistProducts = (item) => {
                router.push({ path: `/powerPage/${item.id}` })
            }

            const selRemindTime=(index)=>{
                state.remindTime=index;
            }
            const showRemindersDialog=()=>{
                state.show=true;
            }
            const confirmRemindDialog=()=>{
                state.show=false;
            }

            const payHandle=()=>{
                if(state.rechargeAmountStatus==false){
                    return;
                }
                if(state.showOverlay==true){
                    Toast('正在处理请稍等')
                    return;
                }
                state.showOverlay=true;
                setTimeout(()=>{
                    state.powerValue=parseInt(state.powerValue)+parseInt(state.rechargeAmount);
                    state.showOverlay=false;
                    state.payResult=true;
                    state.handletype='success';
                    state.powerList.push({info:`充值（${state.payWayTabs[state.active].title}）`,value:'+'+state.rechargeAmount,time:appContext.config.globalProperties.$moment(new Date()).format('YYYY.MM.DD hh:mm')})


                },5000)
            }
            const back=()=>{
                state.handletype='';
                state.payResult=false;
                state.rechargeAmount='';

            }

            const onRechargeAmountInput= () => {
                if(state.rechargeAmount=='') state.rechargeAmountStatus=false;
                else state.rechargeAmountStatus=true;
            }
            const showexchangeCode=()=>{
                state.modal_show=true;
                state.exchangeCode='';
                state.exchangeStatus='0';
                state.modal_show_type='exchangeCode'

            }
            const showCaption=()=>{
                state.modal_show=true;
                state.modal_show_type=''

            }


            return {
                ...toRefs(state),
                goBack,
                showRemindersDialog,
                confirmRemindDialog,
                selRemindTime,
                goToArtistProducts,
                payHandle,
                back,
                onInput,
                exchangeCodeHandle,
                onRechargeAmountInput,
                showexchangeCode,
                showCaption
            }
        },
        components: {
            sHeader
        }
    }
</script>
